.hero-homepage {
    position: relative;

    background-image:
        url(../images/barley.jpg);

    background-size:
        cover;

    background-clip: border-box;
    background-repeat: no-repeat;

    background-position:
        top 0% left 0%;

}

.hero-scene {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: -1;

    .background-layers {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        .layer {
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0;
            background-size: cover;
            transition-property: all;
            transition-duration: 5s;

            &.active {
                opacity: 1;
                background-size: cover;
                transition-property: all;
                transition-duration: 5s;
            }
        }
    }

    .clouds {
        background-image: url(../images/clouds2.png);
        background-repeat: repeat-x;
        background-size: 1600px auto !important;
        background-position: 0 0;
        animation: moving-clouds 180s linear infinite;
        height: 450px;
    }

    .brightner {
        background-color: rgba(255, 255, 255, 0.4);
    }
}

.project-background {
    color: black;
}

.hero-columns {
    width: 80%;
    margin: 0 auto;
}



@keyframes moving-clouds {
    0% {
        background-position:0 100%
    }

    100%{
        background-position:-1600px 100%
    }
}


