// --------------------------------------------- White strig ---------------------------------------
.hero-tagline {
    min-height: 100vh;
    position: relative;
    // padding-bottom: 1em * $body-line-height;
    display: flex;
    align-items: center;


    &-content {
        // margin-top: 130px;
        padding-left: 2.1em;
    }

    .button {
        margin-left: 2rem;
    }
}

.hero-white-backdrop {
    position: absolute;
    height: 100%;
    width: 41.5%;
    background-color: rgba($grey-light, 0.95);
}


h2.tagline-heading {
    font-size: ms(2);
    color: $green !important;
    padding: 1rem;
    margin: 1rem;
    margin-left: 0;
    margin-bottom: 0 !important;
    line-height: 120%;

    span {
        display: block;
        margin-bottom: 0.1em;
    }
    
    font-weight: 400;
    strong {
        box-sizing: content-box;
        text-align: center;
        display: inline-block;
        padding: 0.1em;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        background-color: $green;
        font-weight: 700;
        color: $grey-light !important;
        text-shadow: none;
        margin-right: 0.1em;
        line-height: 100%;
        
    }
}

.tagline-desc {
    color: $grey-dark;
    background-color: white;
    max-width: 100% - 38.2%;
    padding: ms(-1);
    margin: 1rem;
    margin-top: 0;
    min-height: (ms(-1) * 2) + (1.5 * 3);
    border-radius: $radius;
    font-size: ms(1);
}

.tagline-logos {
        display: block;
        margin-top: 1em;
        margin-left: 2em;
        margin-bottom: 2em;
        img {
            display: inline-block;
            margin-right: .5em;
            height: 60px;
            width: auto;
        }
}