h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;	
}

p.content {
	text-align: justify;
}

.section-heading {
	@extend .title;
	font-size: ms(1);
	text-align: center;
	font-weight: 700 !important;

	&:after {
		content: ' ';
		display: block;
		width: 40px;
		margin: 0 auto;

		margin-top: .5rem;

		border-bottom: 3px solid $green;
	}


	&.margin-top {
		margin-top: 2rem;
		
		&-2x {
			margin-top: 3rem;
		}
	}

	&.margin-bottom {
		margin-bottom: 1rem;	

		&-2x {
			margin-bottom: 2rem;
		}

		&-3x {
			margin-bottom: 3rem;
		}
	}
}

.para-summary {
	font-weight: 500;
	font-family: $family-sans-serif;
	font-size: ms(1);
	padding: 1rem 2.5rem;
	border-left: 3px solid rgba($green, 0.3);
}

.para-detailed {
	padding: 1rem;
	background: white;
	position: relative;
	border-radius: $radius;
	border-top-left-radius: 0;

	&:before {
		position: absolute;
		left: -20px;
		top: 0;

		content: '';
		width: 0px;
		height: 0px;
		font-size: 0;
		line-height: 0;
		
		border-top: 0px solid transparent;
		border-bottom: 20px solid transparent; 

		border-right: 20px solid white; 
	}
}

.sub-heading {
	font-size: ms(1);
	font-weight: 600;
	margin-top: 1.5rem;
	margin-bottom: 1rem;

	.team-member-homepage & {
		margin: 0;
	}

	.news-outline-item & {
		margin-top: 0;
	}

	.goal-outline-container & {
		margin-top: 0;
	}
}

.panel-heading {
	font-size: 1.2rem;
	font-weight: 700;
}