$news-outline-slider-controls-opacity: 0.5;

.highlights-container {
	background-color: white;

	.section-controls {
		margin-top: 2rem;
	}
}

.news-outline {
	@extend .is-unselectable;
	
	
	&-item {
		display: block;
		margin: 2.8rem 0;
		padding: 0;
		overflow: hidden;
		background-color: $grey-light;

		transition-property: box-shadow;
		transition-duration: 500ms;
		// @include box-shadow(1);

		&:hover {
			transition-property: box-shadow;
			transition-duration: 500ms;
			@include box-shadow(4);
		}

		.button {
			margin-top: 1rem;
		}

		.padded {
			padding: 1rem;
		}
	}

}

#news_outline {

	.paging-item {
		
		.paging-dot {
			width: 8px !important;
			height: 8px !important;
			background-color: $green !important;
			opacity: $news-outline-slider-controls-opacity;
		}
		
		&:hover .paging-dot {
			opacity: 1;
		}

		&.active .paging-dot {
			transform: scale(1.2);
			background: $green !important;
			opacity: 1;
		}
	}

	.slider-control-centerleft, .slider-control-centerright {

		a {
			color: $border;
			opacity: $news-outline-slider-controls-opacity;

			&:hover {
				opacity: 1;
			}
		}
	}
	
	.slider-control-centerright {
		right: 1rem !important;
	}

	.slider-control-centerleft {
		left: 1rem !important;
	}
}

.news-grid {

	&-item {
		min-height: 300px;
		position: relative;
		background: transparent;
		

		.news-image {
			overflow: hidden;
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;

			z-index: -1;
			
			img {
				min-width: 100%;
				min-height: 100%;
			}

			&::after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-image: linear-gradient(
					rgba(0, 0, 0, 0.5),
					rgba(0, 0, 0, 0.5)
				);
			}
		}

		.news-content {
			color: white;
			position: absolute;
			bottom: 0;
			right: 0;
			left: 0;

			padding: 1rem;

			h2 {
				text-shadow: none;
			}

			a {
				color: white;
				&:hover {
					color: darken(white, 12%);
				}
			}
		}

		.news-heading {
			color: white;
			font-size: $size-large;
		}

		.news-desc {
			max-height: 0px;
			transition-property: max-height;
			transition-duration: 1s;

			overflow: hidden;
		}

		&:hover .news-desc {
			max-height: 150px;
			transition-property: max-height;
			transition-duration: 1s;
		}
	}
}

.news-listing {
	background-color: $grey-light;	

	&--item {
		border-top: 1px solid $grey-light;
		padding: 1rem;
		margin-bottom: .5rem;
		

		img {
			max-width: 75%;
			display:block;
			float: right;
		}
	}

	.para-detailed {
		border-radius: $radius;
		height: 100%;

		&:before {
			content: none;
		}
	}
}

.news-filter-btn {
	width: 150px;
}