$button-family: $family-sans-serif;

.button {
    text-transform: uppercase;
    font-weight: 600;

    @include box-shadow(1);
    transition-property: box-shadow;
    transition-duration: 500ms;
    &:hover {
        @include box-shadow(2);
        transition-property: box-shadow;
        transition-duration: 500ms;
    }
}