.tagline {
    .hero.is-primary & {
        margin-top: 1rem;
        font-weight: bold;
        font-style: normal;
        color: white;

        font-family: $family-sans-serif;
        font-size: 1.5rem;
        text-transform: capitalize;

        // margin-bottom: 150px;
        letter-spacing: 0.05em;
		text-align: center;

        > span {
    	box-shadow: $grey -0.5em -0.5em 0px, $grey 0.5em -0.5em 0px, $grey -0.5em 0.5em 0px, $grey 0.5em 0.5em 0px;

	    background: $grey;

	    	> span {
	    		color: #ffffff;
    			position: relative;
    			text-shadow: none;
	    	}
        }

        // making it a block
        // background-color: rgba($grey-dark, 0.7);
        // padding: 1rem;
        text-shadow: none;
    }
}
